@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600);
.footer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
  margin: 70px 0 0 0;
  background-color: #eee;
  font-weight: 600;
  border-top: 1px solid #662d91;
}

.footer-contact {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.footer-contact a {
  margin: 16px;
}

.footer-images {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}
.footer-images img {
  height: 100px;
  margin: 0 20px;
}

.header {
  display: flex;
  max-width: 1024px;
  height: 100px;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
  letter-spacing: 0.1em;
  font-size: 14px;
  /* margin: 10px 20px; */
  margin: 10px auto;
}

ul {
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  display: flex;
}

li {
  padding: 5px;
  text-align: center;
}

li a {
  color: #999;
  text-decoration: none;
}

.logo {
  height: 100px;
}

.active {
  background-color: #662D91;
  border-radius: 5px;
  -webkit-transition: background-color 1s;
  transition: background-color 1s;
}
.active a {
  color: white;
  -webkit-transition: color 2s;
  transition: color 2s;
}

.not-active a:hover {
  color: black;
}

@media only screen and (max-width: 666px) {
  nav {
    visibility: hidden;
    overflow: hidden;
  }
}

.jumboimg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* transform: scaleX(-1) scaleY(-1);
  z-index: 5; */
}
.jumbotron {
  height: 600px;
}
.jumbotext {
  text-align: center;
  color: white;
  text-shadow: -1px -1px 0 #662D91, 1px -1px 0 #662D91, -1px 1px 0 #662D91,
    1px 1px 0 #662D91;
}
.jumbotext h1 {
  font-size: 80px;
  margin: 10px;
}
.jumbotext h3 {
  margin: 10px;
  font-size: 40px;
}
header {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 600px;
  width: 100%;
}

@media only screen and (max-width: 800px) {
  .jumboimg {
    object-fit: scale-down;
  }
  .jumbotron {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .jumbotext h1 {
    font-size: 80px;
  }
  .jumbotext h3 {
    font-size: 40px;
  }
}

@media only screen and (max-width: 600px) {
  .jumbotext h1 {
    font-size: 40px;
  }
  .jumbotext h3 {
    font-size: 20px;
  }
}

.home {
  height: 100%;
}
.headline {
  width: 50%;
  margin: 60px auto;
  font-weight: 100;
  letter-spacing: 0.5px;
  text-align: center;
  line-height: 1.5;
}
.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  max-width: 1000px;
  margin: 0 auto;
  font-weight: 400;
  text-align: justify;
  line-height: 1.4;
  letter-spacing: 0.1px;
}
.cards img {
  width: 270px;
  height: 240px;
  object-fit: cover;
  margin: 20px 0;
  border-radius: 8px;
}

.cards h2 {
  font-weight: 400;
  text-align: center;
}

.nosework {
  max-width: 270px;
  padding: 5px;
}
.obedience {
  max-width: 270px;
  padding: 5px;
}
.puppy {
  max-width: 270px;
  padding: 5px;
}

.more-info {
  display: flex;
  background-color: white;
  height: 50px;
  border-radius: 5px;
  font-weight: 600;
  text-decoration: none;
  text-align: center;
  color: #662D91;
  border: 2px #662D91 solid;
  width: 160px;
  margin: 60px auto;
  -webkit-transition: all 500ms;
  transition: all 500ms;
}
.more-info:hover {
  color: white;
  background-color: #662D91;
}
.more-info-text {
  margin: auto;
}

hr {
  border-color: #fff;
  max-width: 1000px;
}

.testimonial {
  font-size: 30px;
  font-weight: 200;
  font-style: oblique;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  width: 50%;
  margin: 100px auto;
}
blockquote {
  font-size: 30px;
  font-weight: 200;
  font-style: oblique;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: #f9f9f9;
  border-left: 10px solid #ccc;
  margin: 1.5em 10px 5px 10px;
  padding: 0.5em 10px;
  quotes: '\201C' '\201D' '\2018' '\2019';
}
blockquote:before {
  color: #ccc;
  content: open-quote;
  font-size: 4em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}
blockquote p {
  display: inline;
}
.slide {
  background: #fff !important;
}
.author {
  font-size: 30px;
  font-weight: 200;
  font-style: oblique;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  float: right;
  padding: 0 10px;
  margin: 0;
}
.presentation-mode {
  margin: 20px;
  max-width: 1000px;
  margin: 10px auto;
}

@media only screen and (max-width: 800px) {
  .headline {
    width: 90%;
  }
  .testimonial {
    width: 90%;
  }
  blockquote {
    font-size: 22px;
  }
}
@media only screen and (max-width: 660px) {
  .nosework {
    max-width: none;
    width: 80%;
  }
  .obedience {
    max-width: none;
    width: 80%;
  }
  .puppy {
    max-width: none;
    width: 80%;
  }
  .cards img {
    width: 80vw;
    height: calc(80vw * 0.833);
  }
}
@media only screen and (max-width: 660px) {
  .nosework {
    max-width: none;
    width: 80%;
  }
  .obedience {
    max-width: none;
    width: 80%;
  }
  .puppy {
    max-width: none;
    width: 80%;
  }
  .cards img {
    width: 80vw;
    height: calc(80vw * 0.833);
  }
}

.about {
  display: flex;
  max-width: 1000px;
  margin: 0 auto;
}

.about-photo {
  width: 35%;
  height: 100%;
  padding: 30px;
}

.text-here {
  font-weight: 400;
  letter-spacing: 0.6px;
  color: #444;
  padding: 10px;
}

.text-here h1 {
  font-size: 30px;
  font-weight: 200;
}
.text-here a {
  text-decoration: none;
  color: #999;
}
.text-here a:hover {
  color: #444;
}

@media only screen and (max-width: 640px) {
  .about {
    display: block;
  }
  .about-photo {
    width: 90%;
    margin: 0 auto;
    display: block;
  }
}

.training {
  max-width: 1000px;
  margin: 0 auto;
}
.intro {
  margin: 80px auto;
  text-align: center;
  max-width: 800px;
  padding: 0 50px;
  line-height: 1.5em;
  font-weight: 200;
}
.intro h3 {
  font-size: 30px;
  font-weight: 200;
}
.training-info {
  padding: 0px 20px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-weight: 200;
  line-height: 1.2em;
}
.training-info img {
  width: 40%;
  height: 300px;
  object-fit: contain;
}
.training-info.left {
  flex-wrap: wrap-reverse;
}
.training-info.right {
  flex-wrap: wrap;
}
.training-info h1 {
  margin-bottom: 40px;
}
.training-text {
  width: 50%;
  padding: 30px;
}
.register-button {
  margin: 40px auto;
  display: block;
  text-align: center;
  width: 170px;
  background-color: white;
  border: 2px #662D91 solid;
  font-size: 20px;
  padding: 1em 0;
  border-radius: 5px;
  color: #662D91;
  font-weight: 600;
  text-decoration: none;
  -webkit-transition: all 500ms;
  transition: all 500ms;
}
.register-button:hover {
  background-color: #662D91;
  color: white;
}
.testimonials {
  padding: 30px 0 0 0;
  text-align: center;
}
@media only screen and (max-width: 660px) {
  .training-info img {
    width: 100%;
    height: 60vw;
    margin: 20px auto;
    object-fit: scale-down;
  }
  .training-text {
    text-align: center;
    width: 100%;
    padding: 0 20px;
  }
}

.contact-me {
  padding: 40px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.contact-text {
  text-align: center;
  width: 300px;
}
.contact-me img {
  width: 400px;
  margin: 40px 0;
  object-fit: contain;
}

.message-form {
  width: 80%;
  margin: 0 auto;
}

.message-form input[type='text'] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin: 10px 0;
}
.message-form textarea {
  width: 100%;
  height: 150px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  resize: none;
  margin: 10px 0;
}

.message-form input[type='submit'] {
  width: 100%;
  background-color: #662d91;
  color: white;
  font-weight: 600;
  padding: 14px 20px;
  margin: 8px 0;
  border: 2px solid #662d91;
  border-radius: 4px;
  cursor: pointer;
  -webkit-transition: all 500ms;
  transition: all 500ms;
}

input[type='submit']:hover {
  background-color: white;
  color: #662d91;
}

body {
  font-family: 'Open Sans', sans-serif;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 30px;
  height: 25px;
  right: 36px;
  top: 42px;
  visibility: hidden;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #662d91;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  /* padding: 0.8em; */
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

@media only screen and (max-width: 666px) {
  .bm-burger-button {
    visibility: visible;
  }
  .bm-menu a {
    color: white;
    visibility: visible;
    padding: 5px;
    text-decoration: none;
    font-size: 25px;
  }
}

